.tabsItems {
    display: flex;
    margin-bottom: 50px;
    flex-wrap: wrap;
    gap: 20px;
}

.tabBtn {
    width: 180px;
    height: 50px;
    padding: 5px 10px;
    background-color: transparent;
}

.addBtn:disabled {
    border-radius: 8px;
    box-shadow: inset 5px 5px 6px #999,
    inset -5px -5px 6px #ffffff;
    cursor: pointer;
}

@media screen and ( min-width: 800px ) {
    .tabsItems {
        gap: 80px;
    }
}